import React, { Component, Dispatch } from 'react';
import './mine.styl';
import avatar from '@/assets/imgs/avatar.JPG';
import imgg from '@/assets/imgs/account.png';
import MineItem from '@/components/mine/mineItem';
import Swiper from 'swiper';
import 'swiper/css/swiper.min.css';
import classNames from 'classnames';
import BScroll from 'better-scroll';
import Header from '@/baseUI/header';

import { connect } from 'react-redux';

import { UserInfoState } from '@/store/types';

import { mineItems, MinePageDataItem } from './config';

interface Props {
    userInfo: UserInfoState;
    balance: number;
}
class Mine extends Component<Props> {
    componentDidMount() {
        new Swiper('.slider-container', {
            loop: true,
            autoplay: {
                delay: 2000,
            },
        });
        new BScroll('.bscroll-wrapper', {
            scrollY: true,
            click: true,
            scrollX: false,
        });
    }
    render() {
        let { userInfo } = this.props;
        const {
            accountNumber = '',
            balance,
            userName = '不知名用户',
            avatar,
        } = userInfo;

        const myItems: Array<JSX.Element> = mineItems.map(
            (i: MinePageDataItem) => {
                const { id, title, isBalanceShow, linkTo, isDotShow, icon } = i;

                const myClassName: string = classNames({
                    'mine-item-wrapper': true,
                    hidden: isBalanceShow === 1 ? false : true,
                });
                return (
                    <MineItem
                        title={title}
                        linkTo={linkTo}
                        myClassName={myClassName}
                        isDotShow={isDotShow}
                        id={id}
                        icon={icon}
                        balance={balance}
                        key={id}
                        handleOnClick={() => {}}
                    />
                );
            }
        );
        return (
            <div className="mine">
                <Header middleTitle={'我的'} rightTitle={'&#xe665;'} />
                <div className="bscroll-wrapper">
                    <div className="content">
                        <div className="mine-info-wrapper">
                            <div className="avatar">
                                {avatar ? (
                                    <img src={avatar} alt="图片不见了" />
                                ) : (
                                    <div>未登录</div>
                                )}
                            </div>
                            <div className="name-and-phone">
                                <h3 className="name">{userName}</h3>
                                <div className="phone-and-selfPage">
                                    <div className="phone">
                                        {accountNumber === ''
                                            ? ''
                                            : '用户账号:'}{' '}
                                        {accountNumber}
                                    </div>
                                    <div className="selfPage">
                                        个人主页
                                        <span className="icon iconfont right">
                                            &#xe613;
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="slider-container">
                            <div className="swiper-wrapper">
                                <div className="swiper-slide">
                                    <img
                                        src={imgg}
                                        alt="图片不见了"
                                        width="100%"
                                        height="100%"
                                    />
                                </div>
                            </div>
                        </div>

                        {myItems.length > 0 && myItems}
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        userInfo: state.getIn(['userInfoDataSource']).toJS(),
    };
};
const mapDispatchToProps = (dispatch) => {
    // return {
    //     handleOnClick: (id: number, isDotShow: boolean) => {
    //         // console.log('id为' + id)
    //         if (!isDotShow) {
    //             console.log('因为本身不带有Dot，所以阻断了dispatch请求');
    //             return;
    //         }
    //         dispatch(setIsDotedToFalseActionCreator(id));
    //     },
    // };
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(Mine);
